import { GraphQLError } from 'graphql';

function stringify(obj) {
  return JSON.stringify(obj, null, 2);
}

var formatSingleError = function formatSingleError(error) {
  return Object.assign(Object.assign({}, error), {
    message: error.message,
    stack: error.stack
  });
};

function handleSingleError(error) {
  if (error instanceof GraphQLError) {
    return error.toString();
  }

  if (error instanceof Error) {
    return formatSingleError(error);
  }

  return error;
}

export function formatError(error) {
  if (Array.isArray(error)) {
    return stringify({
      errors: error.map(function (e) {
        return handleSingleError(e);
      })
    });
  }

  return stringify({
    errors: handleSingleError(error)
  });
}
export function formatResult(result) {
  return stringify(result);
}